
body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background-color: #f04;
  min-height: 100vh;
  color: #004;
  font-family: 'Bandeins Sans Variable';
}

h1 {
  font-size: 10rem;
  font-variation-settings: 'wght' 0;
}

ul.info {
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  font-variant-numeric: tabular-nums;
}
